.name-label, .birthday-label, .account-type-label {
  font-family: Poppins;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: -2%;
  border: 5px solid #FFFFFF  
}*/

/* Shared floating label style */
.name-label, .birthday-label, .account-type-label {
  position: absolute;
  top: -7px;                /* Moves label over input border */
  left: 10px;               /* Slight left inset */
  padding: 0 5px;           /* Small padding for white background */
  background: #FFFFFF;      /* To cover input border behind */
  font-size: 10px;
  font-family: Poppins;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: -2%;
  color: #454545;
  z-index: 1;
}


/* Shared input style */
input, select {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  padding: 12px;
  border-radius: 10px;
  box-shadow: 0px 1px 2px 0px #0000000D;
  gap: 30px;
}

/* Specific inputs */
.name-input {
  width: 600px;
  border: 2px solid #650000;
  background: #FFFFFF;
}

.birthday-input {
  width: 160px;
  border: 2px solid #D9D9D9;
}

.account-type-select {
  width: 160px;
  border: 2px solid #D9D9D9;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 15px;
  box-shadow: 0px 1px 2px 0px #0000000D;
  border-radius: 10px;
  font-size: 14px;
}


.search-button {
  width: 52px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid #000000;
  background: #650000;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-button:hover {
  opacity: 0.9;
}

.search-icon {
  width: 23px;
  height: 23px;
}

/* Table Wrapper Styles */
.table-wrapper {
  width: 100%;
  position: relative;
  z-index: 1;
  margin-top: 50px;
}

.table-container {
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #E0D6C0;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  overflow-x: auto;
}

/* Rest of your table styles remain the same */

table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.table-header {
  background: #650000;
  color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.table-header th {
  padding: 15px;
  font-weight: 400;
  font-size: 16px;
}

.row-header-name { width: 20%; text-align: left;
}
.row-header-birthday { width: 15%; text-align: center; }
.row-header-type { width: 10%; text-align: center; }
.row-header-account { width: 20%;   text-align: left;
}
.row-header-balance { width: 10%;   text-align: left;
}
.row-header-actions { width: 25%; text-align: center; }

.row-background-odd {
  background: #FFFFFF;
}

.row-background-even {
  background: #F5F5F5;
}

.row-background-odd td,
.row-background-even td {
  padding: 15px;
  font-size: 14px;
  border-bottom: 1px solid #E0D6C0;
  color: #777777;
}

.col-name { font-weight: 400;width: 20%;  }
.col-birthday { text-align: center; width: 15%;}
.col-type { text-align: center; width: 10%; }
.col-balance { font-weight: 700;width: 10%;  }
.col-account { font-weight: 400; width: 20%;}
.col-actions { 
  text-align: center; 
  width: 25%; 
  color: #650000 !important;
}

.col-actions a {
  color: #650000 !important;
  text-decoration: none;
  margin: 0 5px;
}

.col-actions a:hover {
  text-decoration: underline;
}

/* Responsive Adjustments */
@media (max-width: 1200px) {
  .search-form {
    flex-wrap: wrap;
  }
  
  .form-group {
    min-width: calc(50% - 10px);
  }
}

@media (max-width: 768px) {
  .search-form {
    flex-direction: column;
    align-items: stretch;
  }
  
  .form-group {
    min-width: 100%;
  }
  
  .table-header th {
    font-size: 14px;
    padding: 10px 5px;
  }
  
  .row-background-odd td,
  .row-background-even td {
    padding: 10px 5px;
    font-size: 12px;
  }
}